.projectsContainer{
    width: 100%;
    height: auto;
    padding-top: 6%;
    padding-bottom: 7%;
}

.projectsContainer .projectheading{
    width: 100%;
    text-align: center;
}

.projectsContainer .projectheading h1{
    text-align: center;
    font-family: 'Courgette', cursive;
    font-weight: bolder;
    font-size: 45px;
    width: 200px;
    margin: auto;
    border-bottom: 2px solid rgb(19, 5, 70);
    color: rgb(19, 5, 70);
    margin-bottom: 5px;
}


.projectBox{
    padding: 5%;
}

.projectItem{
    display: flex;
    padding: 5%;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    width: 90%;
    background-color: white;
    border-radius: 17px;
}

.projectItem1{
    display: flex;
    flex-direction: row-reverse;
    padding: 5%;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    width: 90%;
    background-color: white;
    border-radius: 17px;
}

.projectItem:hover{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
    transform: scale(1.06);
    transition: 0.5s ease-in;
}

.projectItem1:hover{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
    transform: scale(1.06);
    transition: 0.5s ease-in;
}

.functionality{
    text-align: left;
}

.projectDetails{
    width: 50%;
    display: block;
    padding-right: 3%;
    text-align: center;
}

.projectDetails > h2{
    color: rgb(46, 11, 39);
}

.projectImage{
    width: 45%;
    display: block;
    height: 400px;
    padding-top: 3%;
    margin-right: 3%;
    margin-left: 3%;
}

.projectImage > img{
    height: 340px;
    width: 100%;
    border: 1px solid rgb(48, 4, 36);
    border-radius: 20px;
}

.desc{
    text-align: left;
}

.desc > span{
   font-size: large;
   font-weight: bold;
}


.techStacks .btn{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.techStacks .btn > button{
   width: 150px;
   margin: 2%;
   border-radius: 10px;
   background-color: rgb(58, 11, 50);
   color: white;
}
.techStacks .btn > button > h3{
    padding: 1%;
    margin: 1%;
}

.techStacks .btn > button > h3 > a{
    text-decoration: none;
    color: white;
}

.techStacks{
    display: flex;
    justify-content: space-around;
}

.techStacks > .tech-stacks{
    display: flex;
    flex-direction: column;
    gap: 3%;
}

.techStacks > .tech-stacks div{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    gap: 3%;
}
.techStacks{
    display: flex;
    gap: 2%;
}

.techStacks > .tools{
    display: flex;
    flex-direction: column;
    gap: 3%;
}

.techStacks > .tools div{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    gap: 3%;
}


@media all and (min-width: 600px) and (max-width: 900px){
    
    .projectDetails{
        width: 55%;
        display: block;
        text-align: center;
        padding-right: 3%;
    }

    .projectsContainer .projectheading h1{
        font-size: 35px;
        width: 150px;
        margin: auto;
    }

    .projectDetails h2{
        font-size: large;
        color: rgb(128, 5, 48);
    }

    .functionality h3{
        font-size: medium;
    }

    .projectItem{
        display: flex;
        width: 90%;
    }
    
    .projectItem1{
        display: flex;
        flex-direction: row-reverse;
        width: 90%;
    }
    
    .projectImage{
        width: 44%;
        margin-top: 5%;
        height: 200px;
    }
    
    .projectImage > img{
        height: 200px;
        width: 100%;
        border-radius: 3px;
        margin-left: 3px;
    }
    
    .desc > span{
       font-size: medium;
       font-weight: bold;
    }
    
    .techStacks .btn{
        display: flex;
        flex-direction: column;
    }
    
    .techStacks .btn > button{
       width: 120px;
       margin: 1%;
       font-size: 10px;
    }

    .techStacks > .tech-stacks div img{
        width: 90px;
    }

    .techStacks > .tech-stacks div img:nth-child(2){
        width: 60px;
    }

    .techStacks > .tech-stacks div img:last-child{
        width: 60px;
    }

    .techStacks > .tech-stacks h3{
        font-size: 12px;
        font-weight: bolder;
    }

    .desc{
        font-size: 13px;
    }

    li{
        font-size: 12px;
    }

    .techStacks > .tools h3{
        font-size: 12px;
        font-weight: bolder;
    }

    .techStacks > .tools div img{
        width: 70px;
    }
}

